#game {
    width: 100%;
    height: 100%;
    max-width: 626px;
    max-height: 365px;
    display: inline-block;
    margin: 0;
    padding: 5px;
    background-color: yellow;
    border-radius: 5px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}